@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);

body {
  background-color: white;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

h1 {
    text-align: left;
}

h2 {
    color: grey;
}
@media screen and (min-width: 857px) {
    a.header-menu-button {
        display: none;
        text-decoration: none;
    }

    div.dropdown-menu {
        display: none;
    }
}

@media screen and (max-width: 640px) {
    div.header-menu-bar{
       width: 600px;
       margin: 10px;
    }

    div.header-menu {
      display: none;
    }

    div.dropdown-menu {
        display: none;
    }

    a.header-menu-button {
        display: inline-block;
        margin: 0 auto;
        padding: 20px 5px 0px 5px;
        text-decoration: none;
    }

    div.dropdown-menu.mobile {
        display: block;
    }

    div.dropdown-menu.mobile a {
        display: block;
        padding: 0px;
    }

}

@media screen and (max-width: 856px) {
    div.header-menu-bar{
       width: 800px;
       margin: 10px;
    }

    div.header-menu {
      display: none;
    }

    div.dropdown-menu {
        display: none;
    }

    a.header-menu-button {
        display: inline-block;
        margin: 0 auto;
        padding: 20px 5px 0px 5px;
        text-decoration: none;
    }
}

nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background: white;
    height: 64px;
    border-radius: 36px;
    position: relative;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    z-index: 1000;
}

nav a {
    color: black;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    font-weight: 600;
    font-size: 20px;
    height: 44px;
    cursor: pointer;
    text-decoration: none;
    transition: 0.5s;
    outline: none;
}

nav a:hover {
    color: #2669f0; 
}

.authorization {
    float: right;
    min-width: 32px;
    height: 32px;
    background: #2669f0;
    border-radius: 16px;
    margin: 16px;
    transition: 0.5s ease;
    cursor: pointer;
    outline: none;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
}

.authorization .material-icons {
    color: white;
    display: inline-block;
    font-size: 28px;
    margin: 2px;
}

.authorization:hover {
    background: #32cbfa;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}

.header-menu {
    height: 64px;
    min-width: 472px;
    width: 640px;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.header-menu a {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    font-weight: 600;
    font-size: 20px;
    color: black;
    display: block;
    height: 44px;
    cursor: pointer;
    text-decoration: none;
    padding: 20px 5px 0px 5px;
    transition: 0.5s;
}

.header-menu a:hover {
    color: #2669f0; 
}

.dropdown-menu.mobile {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    background: white;
    width: 100%;
    border-radius: 32px;
    margin: 72px auto;
    position: absolute;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
    -webkit-animation: fade-bottom ease 0.3s;
            animation: fade-bottom ease 0.3s;
}

.dropdown-menu.mobile a {
    text-align: center;
    padding: 0 16px;
    margin-top: 20px;
}

@-webkit-keyframes fade-bottom {
    from {
      opacity: 0;
      -webkit-transform: translateY(-15px);
              transform: translateY(-15px);
    }
    to {
      opacity: 1;
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }

@keyframes fade-bottom {
    from {
      opacity: 0;
      -webkit-transform: translateY(-15px);
              transform: translateY(-15px);
    }
    to {
      opacity: 1;
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }
.header-back {
    width: 100%;
    background: linear-gradient(135deg, #32cbfa 0%,#2669f0 100%);
    min-height: 131px;
    margin: 0;
    padding-top: 65px;
    overflow: visible;
}
.container {
    width: 70%;
    margin: 0 auto;
    min-width: 300px;
}


.home-button{
    float: left;
    min-width: 32px;
    height: 32px;
    background: #2669f0;
    border-radius: 16px;
    margin: 16px;
    transition: 0.5s ease;
    cursor: pointer;
    outline: none;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
}

.home-button .material-icons {
    color: white;
    display: inline-block;
    font-size: 24px;
    margin: 4px;
}

.home-button:hover {
    background: #32cbfa;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}
.footer-back {
    width: 100%;
    background: rgb(80, 80, 80);
    min-height: 131px;
    margin: 0;
    padding-top: 65px;
    bottom: 0;
}
h1.login {
    padding-top: 20px;
    text-align: center;
}

.login-card {
    margin: 0 auto;
    background-color: white;
    width: 50%;
    min-width: 300px;
    max-width: 400px;
    height: 420px;
    border-radius: 32px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.user-avatar {
    margin: 0 auto;
    background-color: gainsboro;
    height: 100px;
    width: 100px;
    border-radius: 50px;

}

input {
    display: block;
    margin: 20px auto;
    height: 32px;
    width: 200px;
    border: solid 3px gainsboro;
    border-radius: 32px;
    padding: 0 10px;
    outline: none;
    font-size: 16px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue";
}

input:focus {
    border-color: #2669f0;
}

a {
    text-decoration: none;
    transition: 0.5s;
}

.enter-login {
    display: block;
    margin: 20px auto;
    height: 48px;
    width: 128px;
    border: solid 1px #2669f0;
    border-radius: 24px;

    background-color: #2669f0;
    color: white;
    font-size: 16px;
    font-weight: 700;
    text-decoration: none;
    outline: none;
    transition: 0.5s;
}

.enter-login:hover {
    background-color: #32cbfa;
    border-color: #32cbfa;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.registration {
    color: #d8d8d8;
    display: block;
    margin: 20px auto;
    text-align: center;
    font-weight: 700;
    padding-bottom: 20px;
    outline: none;
}

.registration:hover {
    color: #2669f0;
}
.nav-full-screen {
    margin: 0;
    height: 64px;
    width: 100%;
    overflow: auto;
}

h3.home-button-title {
    margin-top: 21px;
}

h3.home-button-title a{
    color: black;
    text-decoration: none;
}

h3.home-button-title a:hover {
    color: #2669f0; 
}

.h1-404 {
    display: block;
    margin: 100px auto;
    text-align: center;
    font-size: 200px;
}

.h2-404 {
    display: block;
    margin: 0 auto;
    text-align: center;
}
.courses-card-grid {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
}
.course-card {
    background-color: white;
    border-radius: 32px;
    width: 200px;
    height: 200px;
    transition: 0.5s;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    margin: 40px 10px;
}

.course-card:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 0 0 100px #2669f060;
}

h3.course-name a {
    display: block;
    color: black;
    text-decoration: none;
    padding-top: 140px;
    text-align: center;
}

h3.course-name a:hover {
    color: #2669f0;
}
@-webkit-keyframes rotating {
    from {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(60deg);
        transform: rotate(60deg);
    }
} 

@keyframes rotating {
    from {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(60deg);
        transform: rotate(60deg);
    }
}

h2.min-width-message {
    color: #2669f0;
    display: block;
    padding: 5px;
    text-align: center;
}

.rotate-image i.material-icons {
    display: block;
    color: #2669f0;
    font-size: 64px;
    margin: 0 auto;
}

.rotate-image {
    height: 64px;
    width: 64px;
    margin: 0 auto;
    -webkit-animation: rotating 1s infinite ease-in-out;
            animation: rotating 1s infinite ease-in-out;
}


